import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';
import { Router } from '@angular/router';
import { UserAccount} from '@models';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';
import { Email } from '../../../../core/models/email.model';
import Swal from 'sweetalert2';
import { NgxSpinnerService } from 'ngx-spinner';

export interface DialogData {
  userAccount: any;
  // userAccount: '';
}
@Component({
  selector: 'app-user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.scss']
})
export class UserListComponent implements OnInit {

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatPaginator, { static: false }) paginator2: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild(MatSort, { static: false }) sort2: MatSort;
  @ViewChild(MatTable, { static: false }) table: MatTable<UserAccount>;
  dataSource: MatTableDataSource<UserAccount>;
  dataSource2: MatTableDataSource<UserAccount>;
  displayedColumns: string[] = [
    'userAvatar',
    'fullName',
    'companyName',
    'status'
    ];

  email = new Email();
  allUserLists = [];
  pendingUserLists = [];

  username: string;

  constructor(
    private apiService: ApiService,
    private router: Router,
    private dataStorage: DataStorageService,
    public dialog: MatDialog,
    private spinner: NgxSpinnerService
  ) { }

  ngOnInit(): void {
    this.getList();
    this.spinner.show();
  }

  getList(): void{
    this.apiService.findAll(ENDPOINTS.userAccount).subscribe(
      (res : any) => {
        this.allUserLists = res.responseData.data;
        // console.log("ALL",this.allUserLists );
        this.dataSource = new MatTableDataSource<UserAccount>(res.responseData.data);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        setTimeout(() => {
          this.spinner.hide();
        }, 1);
      }
    );

    // this.apiService.findAll(ENDPOINTS.getPendingAccounts).subscribe(
    //   (res:any) => {
    //     this.pendingUserLists = res.responseData.data;
    //     this.dataSource2 = new MatTableDataSource<UserAccount>(res.responseData.data);
    //     this.dataSource2.paginator = this.paginator2;
    //     this.dataSource2.sort = this.sort2;
    //   }
    // );
  }

  getUser(id): void{
    this.router.navigate(['/asc/page/account/user-registration']);
    this.dataStorage.saveUserAccountId(id);
  }

  applyFilter1(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
  applyFilter2(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource2.filter = filterValue.trim().toLowerCase();
  }

  openDialog(i:any): void {
   
    if(i.status === "PENDING"){
      const dialogRef = this.dialog.open(UserListDialog, {
        width: '40%',
        height: '100%',
        data: {
          userAccount: i
        }
        // data: {
        //   isFromModal: true,
        //   isUserRegistration: true
        // }
      });
      dialogRef.afterClosed().subscribe(result => {
        console.log('The dialog was closed');
        this.getList();
      });
    }else{
      this.router.navigate(['asc/page/account/user-update/', i.id]);
    }
  }

}


